@import url(https://fonts.googleapis.com/css?family=Raleway:400,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


body{
    display: -webkit-flex;
    display: flex;
    height: 100vh;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
    background: #c0392b;
    font-family: Raleway;
    color: white;
    letter-spacing: 1px;
  }

table {
    max-width: 960px;
    margin: 10px auto;
    color: black;
    font-weight: bold;
  }
  
  caption {
    font-size: 1.6em;
    font-weight: 400;
    padding: 10px 0;
  }
  
  thead th {
    font-weight: 400;
    background: #27ae60;
    color: #FFF;
  }
  
  tr {
    background: #f4f7f8;
    border-bottom: 1px solid #FFF;
    margin-bottom: 5px;
  }
  
  tr:nth-child(even) {
    background: #e8eeef;
  }
  
  th, td {
    text-align: left;
    padding: 20px;
    font-weight: 300;
  }
  
  tfoot tr {
    background: none;
  }
  
  tfoot td {
    padding: 10px 2px;
    font-size: 0.8em;
    font-style: italic;
    color: #8a97a0;
  }
.btn{
    font-size: 18px;
    margin:  0 5px;
    padding: 7px 15px 7px 10px ;
}
.body{
  display: inline;
}
  form{
    display: inherit;
  }
  .card-form{
    
    width: 500px;
    border-radius: 10px;
    background: white;
    box-shadow: 0 27px 55px 0 rgba(0, 0, 0, 0.3), 0 17px 17px 0 rgba(0, 0, 0, 0.15);
  }  
    .form-title{
      width: 100%;
      display: -webkit-flex;
      display: flex;
      -webkit-align-items: center;
              align-items: center;
      -webkit-justify-content: center;
              justify-content: center;
      height: 80px;
      font-size: 20px;
      font-weight: bold;
      background: #27ae60;
      border-radius: 10px 10px 0 0;
    }
    
    .form-body{
      padding: 10px;
    }
      .row{
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: space-around;
                justify-content: space-around;
        padding: 10px;
      }
        input[type="text"],.select{
          -webkit-appearance: none;
                  appearance: none;
          width: 100%;
          margin: 0 15px;
          height: 35px;
          padding: 5px 15px;
          border-radius: 5px;
          outline: none;
          border: none;
          background: #e8ebed;
          color: #576366;
          font-size: 14px;
    }
    
    .rule{
      height: 2px;
      background: #e8ebed;
      margin: 0px 35px;
    }
    
    .form-footer{
      margin: 0 25px 15px 25px;
      padding: 15px 10px;
    }
      a{
        display: inline-block;
        height: 40px;
        line-height: 40px;
        border: none;
        border-radius: 5px;
        padding: 5px 15px;
        background: #27ae60;
        margin-right: 10px;
        font-size: 14px;
      }
      
      span{
        margin-left: 8px;
      }
    
